import classes from './DetailPage.module.scss'
import React, { useEffect, useState } from 'react'
import ReactPlayer from 'react-player'
import { Carousel } from 'react-responsive-carousel'
import '../../carousel.css'

function DetailPage(props) {
  const [currentWork, setCurrentWork] = useState('')
  useEffect(() => {
    const works = [
      {
        id: 0,
        title: 'Chronic disease learning system',
        img: require('../../images/chronic.png')
      },
      {
        id: 1,
        title: 'Treenection',
        img: require('../../images/treeB.JPG')
      },
      // { id: 2, title: 'Swipe Code', img: require('../../images/swp-1.png') },
      {
        id: 3,
        title: 'Physical activity survey',
        img: require('../../images/physicB.png')
      }
    ]
    const currentWork = works.find(work => {
      return work.id === JSON.parse(props.match.params.id)
    })
    setCurrentWork(currentWork)
  }, [])

  return (
    <div className={classes.container}>
      <div className={classes.banner_overlay}>
        <p>{props.match.params.title}</p>
      </div>
      <div
        className={classes.banner}
        style={{ backgroundImage: `url(${currentWork.img})` }}
      ></div>
      <div className={classes.detail}>
        {JSON.parse(props.match.params.id) === 0 ? (
          <div className={classes.wrapper}>
            <div className={classes.title}>Introduction</div>

            <p>
              Chronic disease learning app aim to improve clinical outcomes of
              patients with multiple chronic diseases, by allowing doctors to
              identify potential screeningsthat need to be conducted. What I did
              in this project is to build a SMART-on-FHIR app for doctors to
              identify whether a patient has met the criteria of colorectal
              cancer and cardiovascular disease screening. The criteria should
              be based on the RACGP guidelines provided by the client.
              <ur>
                <li>
                  Allow doctors to select the potential intervention for the
                  patient.{' '}
                </li>
                <li>These interventions are recorded as FHIR resources. </li>
              </ur>
            </p>
            <div className={classes.title}>Development Tools</div>
            <div className={classes.tech}>
              <img
                className={classes.icon}
                src={require('../../images/react.png')}
                alt=""
              />
              <img
                className={classes.icon}
                src={require('../../images/rest.png')}
                alt=""
              />
              <img
                className={classes.icon}
                src={require('../../images/sass.png')}
                alt=""
              />
              <img
                className={classes.icon}
                src={require('../../images/sass.png')}
                alt=""
              />
            </div>
            <div className={classes.title}>Screenshot</div>
            <div>
              <Carousel
                showArrows={true}
                // onChange={onChange}
                // onClickItem={onClickItem}
                // onClickThumb={onClickThumb}
              >
                <div>
                  <img src={require('../../images/2/c4.png')} />
                  <p className="legend">Initial iteration</p>
                </div>
                <div>
                  <img src={require('../../images/2/c1.png')} />
                  <p className="legend">Screening page</p>
                </div>
                <div>
                  <img src={require('../../images/2/c2.png')} />
                  <p className="legend">Criteria of chornic dieseases</p>
                </div>
                <div>
                  <img src={require('../../images/2/c3.png')} />
                  <p className="legend">Desktop-side bar</p>
                </div>
                <div>
                  <img src={require('../../images/2/c4.png')} />
                  <p className="legend">adding or editing current criterias</p>
                </div>
                <div>
                  <p className="legend">Mobile-home page</p>
                </div>
                <div>
                  <p className="legend">Mobile-login</p>
                </div>
              </Carousel>
            </div>
          </div>
        ) : JSON.parse(props.match.params.id) === 1 ? (
          <div className={classes.wrapper}>
            <div className={classes.title}>Introduction</div>
            <p>
              Due to the breakdown of pandemic, people are required to keep a
              social distance between each other. As a result of this, people
              have few chances to start an interpersonal communication with each
              other, which challenge people to maintain or build human
              connection which is an inherent aspect of our society and its
              also highly associated with mental wellness. Thus, how we still
              keeping tied with other human beings has become a significant
              issue need to be addressed in this socially distanced world. The
              <p>
                The concept Treenection was ideated in response to the problem
                space , aiming to facilitate human connection between people
                while keeping safe distance. Treenection is an installation
                which allow people to collaborate together doing activities such
                as stomping. This concept believes providing people opportunity
                to collaborate together could help them form a sense of human
                connection or appreciation to one another .{' '}
              </p>
            </p>
            <div className={classes.title}>Development Tools</div>
            <div className={classes.tech}>
              <img
                className={classes.icon}
                src={require('../../images/arduino.png')}
                alt=""
              />
            </div>
            <div className={classes.title}>Intended experience</div>
            <p>
              Our concept uses the natural structure of three rocks in a
              triangle around a large tree accompanying with simulated natural
              background sounds.Each rock will have two stomping rock sitting
              next to it for people to come along and stomp on, as a way to
              start the interaction. Each rock will be connected by a line of
              led strip which embedded in a wood groove, and the centre of these
              lines will connect with another line to the tree. Each stomping on
              the rock will generate a sound and a pixel in the line next to
              it. Every synchronised stomping with other participant will lead
              to light one pixel in the line connecting with tree. otherwise ,
              people will lose one pixel in the middle line. Once participants
              succeed to light 3 pixels in the middle line, the light around the
              tree will be activated.
              <p>
                There are three different stages of lighting up the three:
                <ur>
                  <li>
                    The led strip wrapping around tree trunk will be lit up.
                  </li>
                  <li>
                    led strip wrapping around tree branches will be lit up.
                  </li>
                  <li>
                    Eventually all the led hanging on the tree will lit up in
                    order to create an immersive experience for users .
                  </li>
                </ur>
              </p>
            </p>
            <p className={classes.title}>Installation introduction</p>
            <div className={classes.video}>
              <ReactPlayer
                url="https://youtu.be/s2Tj921hiRA"
                className="react-player"
                playing
                width="100%"
                height="100%"
              />
            </div>
          </div>
        ) : JSON.parse(props.match.params.id) === 3 ? (
          <div className={classes.wrapper}>
            <p className={classes.title}>Introduction</p>
            <p>
              The value of this instrument is that:
              <ur>
                <li>
                  it can help recognise and showcase teachers efforts to
                  promote physical activity in their schools,
                </li>
                <li>
                  provide formal evidence-based information through a needs
                  assessment exercise that can help teachers/schools meet their
                  States or Territorys physical activity policy and maximise
                  physical activity engagement through improvement of existing
                  physical activity opportunities or the introduction of new
                  ones.
                </li>
                <li>visualise the condition of the physical education</li>
              </ur>
              The information provided by the SPAI can also support funding
              applications for the development or improvement of school physical
              activity programs.
            </p>
            <p className={classes.title}>Design Tools</p>
            <div className={classes.tech}>
              <img
                className={classes.icon}
                src={require('../../images/react.png')}
                alt=""
              />
              <img
                className={classes.icon}
                src={require('../../images/sass.png')}
                alt=""
              />
            </div>
            <p className={classes.title}>Web application</p>
            <div>
              <Carousel
                showArrows={true}
                // onChange={onChange}
                // onClickItem={onClickItem}
                // onClickThumb={onClickThumb}
              >
                <div>
                  <img src={require('../../images/1/home.png')} />
                </div>
                <div>
                  <img src={require('../../images/1/login.png')} />
                </div>
                <div>
                  <img src={require('../../images/1/survey.png')} />
                </div>
                <div>
                  <img src={require('../../images/1/results.png')} />
                </div>
              </Carousel>
            </div>
          </div>
        ) : (
          ''
        )}
      </div>
    </div>
  )
}

export default DetailPage
